.admin-inquiries {
  padding: 20px;
}

.admin-inquiries-table-container {
  background: var(--color-surface);
  border-radius: 8px;
  padding: 20px;
  box-shadow: var(--shadow-card);
}

.admin-inquiries-table {
  width: 100%;
  border-collapse: collapse;
}

.admin-inquiries-table thead {
  background: var(--color-bg);
}

.admin-inquiries-table th {
  padding: 12px;
  text-align: left;
  font-weight: 600;
  font-size: 12px;
  color: var(--color-text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-bottom: 2px solid var(--color-border);
}

.admin-inquiries-table td {
  padding: 16px 12px;
  border-bottom: 1px solid var(--color-border);
  font-size: 14px;
}

.admin-inquiries-table tbody tr:hover {
  background: var(--color-bg);
}

.admin-inquiry-user-cell {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.admin-inquiry-user-name {
  font-weight: 600;
  color: var(--color-text-primary);
}

.admin-inquiry-user-details {
  font-size: 13px;
  color: var(--color-text-secondary);
}

.admin-inquiry-user-email {
  font-size: 12px;
  color: var(--color-text-muted);
}

.admin-inquiry-property-cell {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.admin-inquiry-property-title {
  font-weight: 600;
  color: var(--color-text-primary);
}

.admin-inquiry-property-details {
  font-size: 12px;
  color: var(--color-text-secondary);
}

.admin-inquiry-owner-cell {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.admin-inquiry-owner-name {
  font-weight: 600;
  color: var(--color-text-primary);
}

.admin-inquiry-owner-email {
  font-size: 12px;
  color: var(--color-text-muted);
}

.admin-inquiry-date-cell {
  font-size: 13px;
  color: var(--color-text-secondary);
  white-space: nowrap;
}

.admin-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--color-overlay);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.admin-modal-content {
  background: var(--color-surface);
  border-radius: 8px;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: var(--shadow-card-hover);
}

.admin-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid var(--color-border);
}

.admin-modal-header h2 {
  margin: 0;
  font-size: 20px;
  color: var(--color-text-primary);
}

.admin-modal-close {
  background: none;
  border: none;
  font-size: 28px;
  color: var(--color-text-secondary);
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: all 0.2s;
}

.admin-modal-close:hover {
  background: var(--color-bg);
  color: var(--color-text-primary);
}

.admin-modal-body {
  padding: 20px;
}

.admin-icon-btn.admin-info {
  background: var(--color-info-light);
  color: var(--color-primary);
}

.admin-icon-btn.admin-info:hover {
  background: var(--color-info-light);
}
